import SwiperCore, { Navigation, EffectFade, Autoplay } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/vue';
SwiperCore.use(Navigation, EffectFade, Autoplay);
export default {
  components: {
    Swiper,
    SwiperSlide
  },
  setup() {
    return {
      modules: [Navigation, EffectFade, Autoplay]
    };
  },
  data() {
    return {
      swiper: null,
      swiperOption: {
        effect: 'fade',
        loop: true,
        freeMode: true,
        autoplay: {
          delay: 8000,
          disableOnInteraction: false
        },
        speed: 1000
      },
      swiperList: [{
        img: require('@/assets/images/index/banner01.jpg')
      }, {
        img: require('@/assets/images/index/banner02.jpg')
      }],
      bannerText: {
        topText: '',
        bottomText: ' '
      }
    };
  },
  methods: {
    onSwiper(swiper) {
      this.swiper = swiper;
    }
  }
};