import "core-js/modules/es.array.push.js";
import newsListData from '@/assets/datas/newsListData.json';
import { Swiper, SwiperSlide } from 'swiper/vue';
export default {
  components: {
    Swiper,
    SwiperSlide
  },
  data() {
    return {
      newsListData: newsListData.news,
      swiper: null,
      swiperOption: {
        slidesPerView: 3,
        spaceBetween: 80,
        navigation: {
          nextEl: '.swiper-next-btn',
          prevEl: '.swiper-prev-btn'
        },
        loop: true,
        autoplay: false,
        speed: 800,
        breakpoints: {
          1: {
            slidesPerView: 1,
            spaceBetween: 20
          },
          576: {
            slidesPerView: 2,
            spaceBetween: 30
          },
          992: {
            slidesPerView: 3,
            spaceBetween: 30
          }
        }
      }
    };
  },
  computed: {
    newsList() {
      let ary = [];
      this.newsListData.forEach((element, index) => {
        if (index < 5) ary.push(element);
      });
      return ary;
    }
  },
  methods: {
    onSwiper(swiper) {
      this.swiper = swiper;
    },
    prevFn() {
      this.swiper.slidePrev();
    },
    nextFn() {
      this.swiper.slideNext();
    }
  }
};