export default {
  data() {
    return {
      isShowTip: false,
      isShowAccTip: false,
      acc: null,
      pwd: null,
      checkPwd: null,
      name: null,
      cellphone: null,
      email: null
    };
  },
  computed: {
    isShowRegister() {
      return this.$store.state.isShowRegister;
    }
  },
  methods: {
    clickRegisterBtn() {
      if (this.pwd !== this.checkPwd) {
        this.isShowTip = true;
        this.showFalseAnim();
        return;
      }
      this.$store.dispatch('updateIsShowLoading', 0);
      const memberCheckAcc = process.env.VUE_APP_M_CHECK_ACC;
      this.axios.post(memberCheckAcc, {
        'acc': this.acc
      }).then(response => {
        const backData = response.data;
        if (backData.status === 'success') {
          this.checkAccSucFn();
        } else {
          this.isShowAccTip = true;
          this.$store.dispatch('updateIsShowLoading', 1);
          this.showFalseAnim();
        }
      }).catch(function (error) {
        console.log('error', error);
      });
    },
    checkAccSucFn() {
      const memberRegistnApi = process.env.VUE_APP_M_REGIST;
      this.axios.post(memberRegistnApi, {
        'acc': this.acc,
        'pwd': this.pwd,
        'name': this.name,
        'cellphone': this.cellphone,
        'email': this.email
      }).then(response => {
        this.$store.dispatch('updateIsShowLoading', 1);
        this.registSucFn();
      }).catch(function (error) {
        console.log('error', error);
      });
    },
    registSucFn() {
      this.clickLoginBtn();
      this.$store.dispatch('updateIsShowNotice', true);
      this.$store.dispatch('updateNoticeText', '');
    },
    clickLoginBtn() {
      this.$store.dispatch('updateIsShowLogin', true);
      this.$store.dispatch('updateIsShowRegister', false);
      this.clearForm();
    },
    showFalseAnim() {
      $('.form-section').addClass('animate_headShake');
      $('.form-section').on('webkitAnimationEnd', function () {
        $('.form-section').removeClass('animate_headShake');
      });
    },
    clearForm() {
      this.acc = null;
      this.pwd = null;
      this.checkPwd = null;
      this.name = null;
      this.cellphone = null;
      this.email = null;
      this.isShowTip = false;
      this.isShowAccTip = false;
    },
    focusInputFn(el) {
      $(el.target).closest('.formBox').addClass('inputFocus');
    },
    blurInputFn(el) {
      $(el.target).closest('.formBox').removeClass('inputFocus');
    },
    closeRegister() {
      this.$store.dispatch('updateIsShowRegister', false);
      this.startScrollBar();
      this.clearForm();
    },
    // 
    keydownNull(e) {
      if (e.keyCode == 32) {
        e.returnValue = false;
      }
    },
    // 
    stopScrollBar() {
      var tops = $(document).scrollTop();
      $(document).bind('scroll', function () {
        $(document).scrollTop(tops);
      });
    },
    startScrollBar() {
      $(document).unbind('scroll');
    }
  }
};