import "core-js/modules/es.array.push.js";
import { Autoplay, FreeMode } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/vue';
import foodListData from '@/assets/datas/foodListData.json';
export default {
  components: {
    Swiper,
    SwiperSlide
  },
  setup() {
    return {
      modules: [Autoplay, FreeMode]
    };
  },
  data() {
    return {
      foodListFoodData: foodListData.food,
      foodTitleList: [{
        img: require('@/assets/images/index/delicacy/01.jpg'),
        title: '',
        'class': 'soup'
      }, {
        img: require('@/assets/images/index/delicacy/02.jpg'),
        title: '',
        'class': 'meat'
      }, {
        img: require('@/assets/images/index/delicacy/03.jpg'),
        title: '',
        'class': 'seafood'
      }, {
        img: require('@/assets/images/index/delicacy/04.jpg'),
        title: '',
        'class': 'drink'
      }],
      swiper: null,
      swiperOption: {
        speed: 6000,
        loop: true,
        freeMode: true,
        slidesPerView: 5,
        spaceBetween: 30,
        autoplay: {
          delay: 1,
          stopOnLastSlide: false,
          disableOnInteraction: false
        },
        breakpoints: {
          1: {
            slidesPerView: 3,
            spaceBetween: 15
          },
          580: {
            slidesPerView: 4,
            spaceBetween: 20
          },
          810: {
            slidesPerView: 5,
            spaceBetween: 20
          },
          880: {
            slidesPerView: 4,
            spaceBetween: 30
          },
          1100: {
            slidesPerView: 5,
            spaceBetween: 30
          },
          1400: {
            slidesPerView: 6,
            spaceBetween: 30
          }
        }
      }
    };
  },
  computed: {
    foodMarqueeAry() {
      return this.foodListFoodData.filter(food => {
        return food.name.length === 4;
      });
    },
    typeListAry() {
      let typeAry = [''];
      this.foodListFoodData.forEach(food => {
        if (typeAry.indexOf(food.type) === -1) typeAry.push(food.type);
      });
      return typeAry;
    },
    typeListAryIndex() {
      return function (type) {
        const index = this.typeListAry.indexOf(type);
        return index;
      };
    }
  },
  methods: {
    goTop() {
      $('html,body').scrollTop(0, 0);
    }
  }
};